import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';


const options = {
  headers: new HttpHeaders({ "Content-Type": "application/json" })
}

const base_url = 'https://kevin-csci571-hw8.wl.r.appspot.com/apis';
// const base_url = 'http://localhost:2000/apis';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  constructor(private http: HttpClient) { }

  getSearchResult(query: string) {
    let urlStr: string = base_url + '/search/' + query;
    return this.http.get<any[]>(urlStr);
  }

  getCurrentPlaying() {
    let urlStr: string = base_url + '/current';
    return this.http.get(urlStr);
  }

  getPopular(type: string) {
    let urlStr: string = base_url + '/popular?type=' + type;
    return this.http.get(urlStr);
  }

  getTopRated(type: string) {
    let urlStr: string = base_url + '/toprate?type=' + type;
    return this.http.get(urlStr);
  }

  getTrending(type: string) {
    let urlStr: string = base_url + '/trending?type=' + type;
    return this.http.get(urlStr);
  }

  getVideoAndInfo(id: string, type: string) {
    let urlStr: string = base_url + '/video_and_info?id=' + id + "&type=" + type;
    return this.http.get(urlStr);
  }

  getCastInfo(id: string, type: string) {
    let urlStr: string = base_url + '/cast?id=' + id + "&type=" + type;
    return this.http.get(urlStr);
  }

  getCastDetail(id: string) {
    let urlStr: string = base_url + '/cast_detail?id=' + id;
    return this.http.get(urlStr);
  }

  getReview(id: string, type: string) {
    let urlStr: string = base_url + '/review?id=' + id + "&type=" + type;
    return this.http.get(urlStr);
  }

  getRecommended(id: string, type: string) {
    let urlStr: string = base_url + '/recommended?id=' + id + "&type=" + type;
    return this.http.get(urlStr);
  }

  getSimilar(id: string, type: string) {
    let urlStr: string = base_url + '/similar?id=' + id + "&type=" + type;
    return this.http.get(urlStr);
  }

}
