import { MobileconfiService } from './../mobileconfig.service';
import { Component, OnInit, AfterViewInit, AfterViewChecked } from '@angular/core';


@Component({
  selector: 'app-mylist',
  templateUrl: './mylist.component.html',
  styleUrls: ['./mylist.component.css']
})
export class MylistComponent implements OnInit, AfterViewInit, AfterViewChecked{

  images: string[][] = [];
  titles: string[][] = [];
  ids: string[][] = [];
  types: string[][] = [];
  main_title!: string;

  constructor(private mobile_service: MobileconfiService) { }

  ngOnInit(): void {
    let list = JSON.parse(localStorage.getItem('watchlist')!);
    if (list == null || list.length == 0) {
      this.main_title = 'No items found in Watchlist.';
      $("h1").css({"margin-top":"10%","text-align":"center"});
    } else {
      this.main_title = 'My WatchList';
      let count = list.length;
      let image_array: string[] = [], title_array: string[] = [], id_array: string[] = [], type_array: string[] = [];
      let index = count - 1, num = 0;
      while (index >= 0) {
        image_array.push(list[index].url);
        title_array.push(list[index].name);
        type_array.push(list[index].type);
        id_array.push(list[index--].id);
        num++;
        if (num % 6 == 0 || index == -1) {
          this.images.push(image_array);
          this.titles.push(title_array);
          this.ids.push(id_array);
          this.types.push(type_array);
          image_array = [];
          title_array = [];
          id_array = [];
          type_array = [];
        }
      }
    }
  }

  ngAfterViewInit(){
    
  }

  ngAfterViewChecked() {
    this.mobile_service.footerPosition();
  }
}
