import { Component, OnInit, Input, ViewEncapsulation, HostListener } from '@angular/core';
import { Observable, of, OperatorFunction } from 'rxjs';
import { catchError, debounceTime, distinctUntilChanged, map, tap, switchMap } from 'rxjs/operators';
import { HttpService } from './../http.service';
import { NgbModalConfig, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
  encapsulation: ViewEncapsulation.None,
  styles: [`.form-control { width: 300px; }`]
})
export class SearchComponent implements OnInit {

  public model: any;

  formatter = (x: { name: string }) => '';

  search = (text$: Observable<string>) =>
    text$.pipe(
      debounceTime(200),
      distinctUntilChanged(),
      switchMap(term => this.service.getSearchResult(term).pipe(
        tap(),
        catchError(() => {
          return of([]);
        }))
      )
    )

  constructor(private service: HttpService, private router: Router) { }

  @HostListener('click', ['$event'])
  onClick(event: any) {
    if (typeof this.model != 'undefined' && this.model.id != null) {
      // window.location.href = "/watch/" + this.model.media_type + "/" + this.model.id;
      this.router.navigate(["/watch/" + this.model.media_type + "/" + this.model.id]);
      this.model.id = null;

      $(".navbar-collapse").removeClass('show');
    }
  }

  ngOnInit(): void {

  }

}
