import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LocalstorageService {

  constructor() { }

  addLocalStorage(id: string, type: string, name: string, url: string, key: string) {
    let list: Card[] = JSON.parse(localStorage.getItem(key)!);
    if (list == null) {
      list = new Array<Card>();
      list.push(new Card(id, type, name, url));
      localStorage.setItem(key, JSON.stringify(list));
    } else {
      let temp: Card[] = new Array<Card>();
      for (let i = 0; i < list.length; i++) {
        if (list[i].id != id) {
          temp.push(list[i]);
        }
      }
      temp.push(new Card(id, type, name, url));
      localStorage.setItem(key, JSON.stringify(temp));
    }
  }

  removeLocalStorage(id: string) {
    let list: Card[] = JSON.parse(localStorage.getItem("watchlist")!);
    let temp: Card[] = new Array<Card>();
    for (let i = 0; i < list.length; i++) {
      if (list[i].id != id) {
        temp.push(list[i]);
      }
    }
    localStorage.setItem('watchlist', JSON.stringify(temp));
  }

  isContain(id: string) : boolean{
    let list: Card[] = JSON.parse(localStorage.getItem("watchlist")!);
    if(list == null) {
      return false;
    }
    for (let i = 0; i < list.length; i++) {
      if (list[i].id == id) {
        return true;
      }
    }
    return false;
  }

  isContinueNull(): boolean {
    if(JSON.parse(localStorage.getItem("continue")!)==null) {
      return true;
    }
    return false;
  }
}

export class Card {
  id: string; type: string; name: string; url: string;

  constructor(id: string, type: string, name: string, url: string) {
    this.id = id;
    this.type = type;
    this.name = name;
    this.url = url;
  }
}
