import { MobileconfiService } from './../mobileconfig.service';
import { LocalstorageService } from './../localstorage.service';
import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { HttpService } from './../http.service';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { NgbModalConfig, NgbModal, NgbAlert } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.css'],
  providers: [NgbModalConfig, NgbModal],
  encapsulation: ViewEncapsulation.None,
})
export class DetailComponent implements OnInit {

  private _success = new Subject<string>();

  message = '';

  @ViewChild('selfClosingAlert', { static: false })
  selfClosingAlert!: NgbAlert;

  constructor(private route: ActivatedRoute, private service: HttpService,
    config: NgbModalConfig, private modalService: NgbModal, private storageService: LocalstorageService,
    private mobile_service: MobileconfiService) {
  }

  isAdded!: boolean;
  alert_type!: string;
  btn_message!: string;
  alert_message!: string;

  type!: string;
  id!: string;

  video_width!: number;
  // video_height : string;

  response: any;
  video: any;
  detail: any;
  video_src!: string;
  title!: string;
  tagline!: string;
  genres!: string[];
  spoken_languages!: string[];
  overview!: string;
  vote_average!: string;
  date!: string;
  time!: string;
  poster_path!: string;

  cast_response: any;
  cast_array!: Array<Cast>;

  target: any;
  cast_id: any;
  image_selected: any;

  cast_detail_response: any;
  cast_detail!: CastDetail;

  review_response: any;
  reviews!: Array<Review>;

  length!: number;

  isMobile: boolean = false;

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      
      this.isMobile = this.mobile_service.isMobile();
      if(this.isMobile) {
        this.video_width = 330;
      } else {
        this.video_width = this.mobile_service.setWidth(0.65);
      }

      $('html,body').animate({ scrollTop: 0 }, 500);
      this.type = params['type'];
      this.id = params['id'];

      this.isAdded = this.storageService.isContain(this.id);

      this.transferType();

      // console.log(this.type + "  " + this.id);
      this.service.getVideoAndInfo(this.id, this.type).subscribe(response => {
        this.response = response;
        console.log(this.response);
        this.video = this.response.video;
        this.detail = this.response.detail;

        this.video_src = this.video.key;
        this.title = this.detail.title;
        this.tagline = this.detail.tagline;
        this.genres = this.detail.genres;
        this.spoken_languages = this.detail.spoken_languages;
        this.overview = this.detail.overview;
        this.vote_average = this.detail.vote_average;
        this.date = this.detail.date;
        this.time = this.detail.time;
        this.poster_path = this.detail.poster_path;

        this.storageService.addLocalStorage(this.id, this.type, this.title, this.poster_path, 'continue');
        // console.log(JSON.parse(localStorage.getItem('continue')!));
      });
      this.service.getCastInfo(this.id, this.type).subscribe(response => {
        this.cast_array = new Array<Cast>();
        this.cast_response = response;
        // console.log(this.cast_response.length);
        for (var i = 0; i < this.cast_response.length; i++) {
          this.cast_array.push(new Cast(this.cast_response[i].id, this.cast_response[i].name, this.cast_response[i].character, this.cast_response[i].profile_path));
        }
        this.service.getReview(this.id, this.type).subscribe(res => {
          this.reviews = new Array<Review>();
          this.review_response = res;
          for (var i = 0; i < this.review_response.length; i++) {
            let r = this.review_response[i];
            this.reviews.push(new Review(r.author, r.rating, r.avatar_path, r.content, r.created_at, r.url));
          }
          this.length = this.reviews.length;
          // this.mobile_service.footerPosition();
        });
      });
    });

    this._success.subscribe(message => this.message = message);
    this._success.pipe(debounceTime(5000)).subscribe(() => {
      if (this.selfClosingAlert) {
        this.selfClosingAlert.close();
      }
    });
  }

  public changeSuccessMessage() {
    if(this.isAdded) {
      this.storageService.removeLocalStorage(this.id);
    } else {
      this.storageService.addLocalStorage(this.id, this.type, this.title, this.poster_path, 'watchlist');
    }
    this.isAdded = !this.isAdded;
    this.transferType();
    this._success.next(this.alert_message);

    // console.log(JSON.parse(localStorage.getItem('watchlist')!));
  }

  open(content: any, e: Event) {
    this.target = e.target;
    this.cast_id = this.target.id;
    // console.log(this.cast_id);
    for (var i = 0; i < this.cast_array.length; i++) {
      if (this.cast_id == this.cast_array[i].id) {
        this.image_selected = this.cast_array[i].profile_path;
      }
    }

    this.service.getCastDetail(this.cast_id).subscribe(res => {
      this.cast_detail_response = res;
      // console.log(this.cast_detail_response);
      this.cast_detail = new CastDetail(
        this.cast_detail_response.detail.birthday,
        this.cast_detail_response.detail.place_of_birth,
        this.cast_detail_response.detail.name,
        this.cast_detail_response.detail.homepage,
        this.cast_detail_response.detail.gender,
        this.cast_detail_response.detail.also_known_as,
        this.cast_detail_response.detail.known_for_department,
        this.cast_detail_response.detail.biography,
        this.cast_detail_response.external.imdb_id,
        this.cast_detail_response.external.facebook_id,
        this.cast_detail_response.external.instagram_id,
        this.cast_detail_response.external.twitter_id,
        this.cast_detail_response.detail.profile_path);
      // console.log(this.cast_detail);
      this.modalService.open(content, { size: 'lg', centered: true });
      // console.log(this.cast_detail);
    });
  }

  transferType() {
    if (this.isAdded) {
      this.alert_type = 'success';
      this.btn_message = 'Remove from Watchlist';
      this.alert_message = 'Added to watchlist.';
    } else {
      this.alert_type = 'danger';
      this.btn_message = 'Add to Watchlist';
      this.alert_message = 'Removed from watchlist.';
    }
  }

}

class Cast {
  id: string;
  name: string;
  character: string;
  profile_path: string;

  constructor(id: string, name: string, character: string, profile_path: string) {
    this.id = id;
    this.name = name;
    this.character = character;
    this.profile_path = profile_path;
  }
}

class CastDetail {
  birthday: string;
  place_of_birth: string;
  name: string;
  homepage: string;
  gender: string;
  also_known_as: string[];
  known_for_department: string;
  biography: string;
  imdb_id: string;
  facebook_id: string;
  instagram_id: string;
  twitter_id: string;
  profile_path: string;

  constructor(birthday: string, place_of_birth: string, name: string, homepage: string, gender: string, also_known_as: string[],
    known_for_department: string, biography: string, imdb_id: string, facebook_id: string, instagram_id: string, twitter_id: string, profile_path: string) {
    this.birthday = birthday;
    this.place_of_birth = place_of_birth;
    this.name = name;
    this.homepage = homepage;
    this.gender = gender;
    this.also_known_as = also_known_as;
    this.known_for_department = known_for_department;
    this.biography = biography;
    this.imdb_id = imdb_id;
    this.facebook_id = facebook_id;
    this.instagram_id = instagram_id;
    this.twitter_id = twitter_id;
    this.profile_path = profile_path;
  }
}

class Review {
  author: string;
  rating: number;
  avatar_path: string;
  content: string;
  created_at: string;
  url: string;

  constructor(author: string, rating: number, avatar_path: string, content: string, created_at: string, url: string,) {
    this.author = author;
    this.rating = rating;
    this.avatar_path = avatar_path;
    this.content = content;
    this.created_at = created_at;
    this.url = url;
  }
}
